  background-size: cover;
  background-repeat: no-repeat;
  background-position: center top; */
  text-align: center;
  /* min-height:100vh; */
}

h2 {

  color: white;
  margin: 0;
  margin-bottom: 10px;
}

.bg {

  margin: 0;
  padding: 0;
  z-index: 0;
  display: block;
  position: fixed;
  top: 0;
  left: 0;
  width:100%;
  height:100%;

  font-family: sans-serif;
  background-image: url(images/bg4.png);
  background-attachment: fixed;
  background-size: cover;
  -webkit-transform: scale(1.1);
      -ms-transform: scale(1.1);
          transform: scale(1.1);
  background-repeat: no-repeat;
  background-position: center top;
  text-align: center;
  min-height:100vh;

}

.bg.blur {
  -webkit-animation: bgBlur 3s both;
  animation: bgBlur 3s both;
}

.bg.noBlur {
  -webkit-filter: blur(10px);
  -moz-filter: blur(10px);
  -o-filter: blur(10px);
  -ms-filter: blur(10px);
  -webkit-animation: nonBlur 3s both;
  animation: nonBlur 3s both;
}

@-webkit-keyframes bgBlur {
  100% {
    -webkit-filter: blur(10px);
    -moz-filter: blur(10px);
    -o-filter: blur(10px);
    -ms-filter: blur(10px);
    filter: blur(10px);

  }
}

@keyframes bgBlur {
  100% {
    -webkit-filter: blur(10px);
    -moz-filter: blur(10px);
    -o-filter: blur(10px);
    -ms-filter: blur(10px);
    filter: blur(10px);

  }
}

@-webkit-keyframes nonBlur {
  100% {
    -webkit-filter: blur(0px);
    -moz-filter: blur(0px);
    -o-filter: blur(0px);
    -ms-filter: blur(0px);
    filter: blur(0px);

  }
}

@keyframes nonBlur {
  100% {
    -webkit-filter: blur(0px);
    -moz-filter: blur(0px);
    -o-filter: blur(0px);
    -ms-filter: blur(0px);
    filter: blur(0px);

  }
}



